// 导入 eventBus 模块
import bus from './eventBus';
import { getArtistAlbum, getArtistMv, getArtistDesc, getArtistSimi, getArtistTopSong } from '@/request/api/singerPage';
import { getAlbumSongs } from '@/request/api/songListDetail';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 歌手id
      singerId: 0,
      // 歌手名
      singerName: '',
      // 歌手专辑数
      albumSize: 0,
      // 当前 tab，默认是专辑(1)
      singerAchievementActive: 1,
      // 排列模式
      modes: [{
        id: 1,
        icon: '#icon-caidan',
        title: '大图模式'
      }, {
        id: 2,
        icon: '#icon-liebiaomoshi',
        title: '列表模式'
      }, {
        id: 3,
        icon: '#icon-shituliebiaoList',
        title: '图列模式'
      }],
      // 当前排列模式(大图、列表、图列模式)
      nowArrange: 1,
      // 歌手专辑
      singerAlbums: [],
      // 页面展示的歌单列表
      // showingLists: [],
      // 图列模式每歌单前十首
      showingTenSongs: [],
      // 歌手 mv数量
      mvSize: 0,
      // 歌手 mv
      mvs: [],
      // 请求回来的原数据
      resData: [],
      // 渲染总数
      total: 0,
      // 每次插入渲染的 DOM 数量
      once: 10,
      // 当前已经渲染 DOM 的总数，从 25 开始，因为一开始已经渲染了 25 个 Mv
      countOfRender: 25,
      // 歌手简介
      briefDesc: '',
      // 歌手介绍
      introduction: [],
      // 相似歌手
      artistsSimi: [],
      // 歌手热门 50首
      topSongs: [],
      // 热门 50首前十首
      topTenSongs: [],
      // 在页面的热门歌曲
      showingTopSongs: []
    };
  },

  // 接收父组件传过来的值
  props: ['scrollTop'],

  // 写在 created 阶段（现成的解决方案）
  created() {
    // 接收从兄弟组件 SingerInfo 传过来的歌手 id、歌手名和 mv数量，页面切换歌手即收到兄弟组件传过来的参数
    bus.on('singerInfo', val => {
      // console.log('从兄弟组件接收多个参数', val);
      this.singerId = val.singerId;
      this.singerName = val.singerName;
      this.mvSize = val.mvSize;
      this.albumSize = val.albumSize; // console.log(this.singerId, this.singerName, this.mvSize, this.albumSize);
    });
  },

  mounted() {
    // 获取歌手专辑，需要先等歌手 id从兄弟组件传过来
    setTimeout(() => this.getArtistAlbum(), 500);
  },

  methods: {
    // 选择当前 Tab
    tabExclusive(n) {
      this.singerAchievementActive = n;
    },

    // 鼠标移入排列
    arrangeMouseenter(id) {
      // 查看有 arrangeActive 类，classList[] 是伪数组，先用 Array.from 转为真数组，再用 find() 查找数组元素
      const isActive = Array.from(this.$refs.arrange[id - 1].classList).find(item => item == 'arrangeActive');
      if (!isActive) this.$refs.arrange[id - 1].classList.add('arrangeMouse');
    },

    // 鼠标移出排列
    arrangeMouseleave(id) {
      const isActive = Array.from(this.$refs.arrange[id - 1].classList).find(item => item == 'arrangeActive');
      if (!isActive) this.$refs.arrange[id - 1].classList.remove('arrangeMouse');
    },

    // 更换排列方式
    changeArrange(type) {
      this.nowArrange = type;
    },

    // 获取歌手专辑
    async getArtistAlbum() {
      // 根据歌手专辑数量请求
      const res = await getArtistAlbum(this.singerId, sessionStorage.getItem("cookie"), this.albumSize); // console.log('获取歌手专辑', res);

      this.singerAlbums = res.data.hotAlbums; // console.log('歌手专辑', this.singerAlbums);

      if (this.singerAlbums.length !== 0) this.isLoading = 0;
    },

    // 图列模式前十首处理
    async tenSongsHandle() {
      // ！只在第一次获取专辑歌曲
      if (this.showingTenSongs.length == 0) {
        // 获取所有专辑的歌曲
        for (var i = 0; i < this.singerAlbums.length; i++) {
          const res = await getAlbumSongs(this.singerAlbums[i].id, sessionStorage.getItem("cookie")); // console.log('专辑歌曲', res);
          // 截取前十首

          this.showingTenSongs.push(res.data.songs.slice(0, 10));
        } // console.log('showingTenSongs', this.showingTenSongs);

      }
    },

    // 歌单显示播放键
    playKeyMouseenter(index) {
      this.$refs.playKey[index].classList = 'playKey';
    },

    // 歌单隐藏播放键
    playKeyMouseleave(index) {
      this.$refs.playKey[index].classList = 'playKey1';
    },

    // 去往专辑详情页
    goAlbumDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 2
        }
      });
    },

    // 时间戳转专辑发布日期
    formatPublishTime(time) {
      return this.$formatDate(time);
    },

    // 格式化单曲序号
    formatIndex(index) {
      return this.$formatIndex(index);
    },

    // 获取歌手 mv
    async getArtistMv() {
      const res = await getArtistMv(this.singerId, sessionStorage.getItem("cookie"), this.mvSize); // console.log('获取歌手 mv', res);
      // 歌手没 Mv：

      if (res.data.mvs.length == 0) this.isLoading = 0; // 歌手有 Mv：
      // this.mvs = res.data.mvs
      // console.log('歌手 mv', this.mvs);
      // 把请求回来的原数据放到存放原数据的暂存数组中(该数组不是渲染数据的数组)

      this.resData = res.data.mvs; // console.log('暂存数据数组：', this.resData);
      // 渲染总数

      this.total = this.resData.length; // 刚开始只渲染 25 条数据

      this.mvs = this.resData.slice(0, 25);
      if (this.mvs.length !== 0) this.isLoading = 0; // 重置当前已经渲染 DOM 的总数

      this.countOfRender = 25;
    },

    // 每次只渲染指定条 mv数据(每次只加指定条数据到渲染数组中)
    async scrollLoad() {
      // console.log('this.countOfRender:', this.countOfRender, 'this.total:', this.total);
      const temp = [];

      for (let i = 0; i < this.once; i++) {
        // 当 DOM 渲染完就退出
        if (this.countOfRender >= this.total) return; // 还没渲染完继续添加
        else {
          // 把请求回来的原数据先加到暂存数组中
          temp.push(this.resData[this.countOfRender]); // 当前已经渲染 DOM 的总数

          this.countOfRender += 1;
        }
      } // 然后把暂存数组的数据加到需要渲染的数组中，concat() 将一个数组合并到另一个数组


      this.mvs = this.mvs.concat(temp); // console.log('temp:', temp);
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 获取歌手描述
    async getArtistDesc() {
      const res = await getArtistDesc(this.singerId, sessionStorage.getItem("cookie")); // console.log('获取歌手描述', res);

      this.briefDesc = res.data.briefDesc;
      this.introduction = res.data.introduction; // console.log('歌手简介', this.briefDesc);
      // console.log('歌手介绍', this.introduction);

      if (this.briefDesc !== '') this.isLoading = 0;
    },

    // 分割歌手介绍
    introductionSeparate(introductionItem) {
      let arr; // console.log('introductionItem', introductionItem);
      // 以换行符分割每一段，每个 item 就是一段

      arr = introductionItem.split(/[(\r\n)\r\n]+/).map(item => item); // bug: 换了 Tab 之后又会调用到该函数
      // console.log('arr', arr);

      return arr;
    },

    // 获取相似歌手
    async getArtistSimi() {
      const res = await getArtistSimi(this.singerId, sessionStorage.getItem("cookie")); // console.log('获取相似歌手', res);

      this.artistsSimi = res.data.artists; // console.log('相似歌手', this.artistsSimi);

      if (this.artistsSimi.length !== 0) this.isLoading = 0;
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
      setTimeout(() => this.$router.go(0), 500);
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      // bug: Uncaught TypeError: Cannot read properties of null (reading 'scrollTop') at Proxy.handleScroll (SingerPage.vue:33:1)
      // 父组件的事件监听函数无法正常移出
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          // 1是 Mv，2是视频
          type: 1
        }
      });
    },

    // 获取歌手热门 50 首歌曲
    async getArtistTopSong() {
      const res = await getArtistTopSong(this.singerId, sessionStorage.getItem('cookie')); // console.log('获取歌手热门 50 首歌曲', res);

      this.topSongs = res.data.songs; // console.log('歌手热门 50 首歌曲', this.topSongs);

      this.topTenSongs = this.topSongs.slice(0, 10); // console.log('topTenSongs:', this.topTenSongs);

      this.showingTopSongs = this.topTenSongs;
    },

    // 展示所有热门 50 首(切换页面需要收回)
    showTop50() {
      // 通过覆盖展示全部歌曲
      this.showingTopSongs = this.topSongs;
    }

  },
  watch: {
    // 切换当前 tab，默认是专辑(1)
    singerAchievementActive() {
      // 收回全部 50首，只展示前十首
      if (this.showingTopSongs.length > 10 && this.singerAchievementActive !== 1) {
        this.showingTopSongs = this.topTenSongs;
      } // 只获取一次


      if (this.singerAchievementActive == 2 && this.mvs.length == 0) {
        this.isLoading = 1; // 获取歌手 mv

        this.getArtistMv();
      } else if (this.singerAchievementActive == 3 && this.briefDesc == '' && this.introduction.length == 0) {
        this.isLoading = 1; // 获取歌手描述

        this.getArtistDesc();
      } else if (this.singerAchievementActive == 4 && this.artistsSimi.length == 0) {
        this.isLoading = 1; // 获取相似歌手

        this.getArtistSimi();
      }
    },

    // 当前排列
    nowArrange() {
      // 图列模式前十首处理
      if (this.nowArrange == 3) {
        // 获取歌手热门 50 首歌曲
        this.getArtistTopSong(); // 图列模式前十首处理

        this.tenSongsHandle();
      }
    },

    // 监听从父组件 DiscoverSinger.vue 接收回来的 scrollTop 
    scrollTop() {
      // console.log('子：' + this.scrollTop);
      this.scrollLoad();
    },

    // 监听路由参数 $route，路由参数改变则重新请求数据 和页面重新渲染
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // 获取歌手专辑，需要先等歌手 id从兄弟组件传过来
      setTimeout(() => this.getArtistAlbum(), 500);
    }
  }
};