// 导入 eventBus 模块
import bus from './eventBus';
import { getArtistDetail } from '@/request/api/singerPage';
export default {
  data() {
    return {
      // 歌手 id
      id: 0,
      // 歌手详情
      artistDetail: {},
      // artist里的信息
      artist: {},
      // 用户 id
      userId: 0
    };
  },

  mounted() {
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    // 相当于是父组件接收的路由参数 query
    this.id = this.$route.query.id; // 获取歌手详情(短时间内只获取一次，artistDetail是空对象)
    // 使用 Object.keys()方法获取对象的所有属性名，并将其返回为一个数组

    if (this.id !== 0 && Object.keys(this.artistDetail).length == 0) this.getArtistDetail(this.id);
  },

  methods: {
    // 去往个人主页
    gotoPersonalHome() {
      if (this.id !== 0) {
        this.$router.push({
          path: '/PersonalHome',
          query: {
            // 这里使用用户id，不是歌手id
            userId: this.artistDetail.user.userId
          }
        });
      }
    },

    // 获取歌手详情
    async getArtistDetail(id) {
      const res = await getArtistDetail(id, sessionStorage.getItem("cookie")); // console.log('获取歌手详情', res);

      this.artistDetail = res.data.data; // console.log('歌手详情', this.artistDetail);

      this.userId = this.artistDetail.user ? this.artistDetail.user.userId : 0; // artist里的信息

      this.artist = this.artistDetail.artist; // console.log('this.artist', this.artist);
      // 以对象形式传递多个参数 给兄弟组件 SingerAchievement 组件传歌手 id、歌手名和 mv数量

      bus.emit('singerInfo', {
        singerId: this.artist.id,
        singerName: this.artist.name,
        mvSize: this.artist.mvSize,
        albumSize: this.artist.albumSize
      });
    }

  },
  watch: {
    // 监听路由参数 $route，路由参数改变则重新请求数据 和页面重新渲染
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // console.log('newRoute:', newRoute.query.id, 'oldRoute:', oldRoute.query.id);
      this.id = newRoute.query.id; // 获取歌手详情

      this.getArtistDetail(this.id);
    }
  }
};