import SingerInfo from '@/components/singerPage/SingerInfo.vue';
import SingerAchievement from '@/components/singerPage/SingerAchievement.vue';
export default {
  data() {
    return {
      // 传给子组件的
      scrollTop: 0
    };
  },

  components: {
    SingerInfo,
    SingerAchievement
  },

  created() {
    this.listenerFunction();
  },

  beforeDestroy() {
    // 去除监听器
    document.removeEventListener("scroll", this.listenerFunction);
  },

  methods: {
    // 给监听器绑定滚动方法 handleScroll()
    listenerFunction(e) {
      document.addEventListener('scroll', this.handleScroll, true);
    },

    // 滚动方法
    handleScroll() {
      let a = this.$refs.singerPage.scrollTop; // 每滚动 400 输出一次，SingerAchievement 大约为 2 行 MV 的距离

      if (a % 400 == 0) {
        this.scrollTop = a; // console.log('父：' + this.scrollTop);
      }
    }

  }
};